.navContainer {
  background-color: white;
  padding-left: 2rem;
  padding-right: 2rem;
  width: 100%;
  .Menu {
    display: none;
    left: -100%;
    grid-template-rows: 100px 90%;
    position: absolute;
    z-index: 2;
    background: white;
    width: 250px;
    height: 100%;
    box-shadow: 0 2rem 3rem $color-background-white;
    animation: showMenu 400ms ease forwards;
    @keyframes showMenu {
      to {
        left: 0;
      }
    }
    .TitleMenu {
      color: blue;
    }
    a {
      color: black;
      &:hover {
        color: blue;
      }
    }
    .TitleMenuContainer {
      grid-row: 1;
      display: flex;
      align-items: center;
      margin-left: 10px;
      .close {
        cursor: pointer;
        margin-left: 45px;
        margin-top: 10px;
      }
    }
    .Bot {
      grid-row: 2;
      display: flex;
      flex-direction: column;
      margin-left: 10px;
      .active {
        color: blue;
      }
    }
  }
  .NavInContainer {
    width: 100%;
    height: 90px;
    background-color: white;
    display: grid;
    grid-template-columns: 1fr 1fr;
    .left {
      cursor: pointer;
      display: flex;
      justify-content: left;
      align-items: center;
      color: blue;
      margin-left: 30px;
      .openMenu {
        display: none;
        cursor: pointer;
        #openMenuButton {
          font-size: 50px;
        }
      }
      h2 {
        padding-right: 0px;
      }
    }
    .linkDiv {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
    }

    h2 {
      padding-right: 0px;
    }

    .MenuButton {
      display: none;
    }
    h2 {
      color: $color-1;
      padding-right: 40px;
    }
    li {
      padding-left: 40px;
      padding-right: 40px;
      font-size: 1.25rem;
      color: black;
      transition: 0.2s;
    }

    li:hover {
      color: blue;
      cursor: pointer;
    }
  }
  .Right {
    grid-column: 2;
    display: flex;
    align-items: center;
    justify-content: right;
    button {
      width: 200px;
      height: 50px;
      margin-left: 2rem;
      border-radius: 15px;
      border-style: none;
      color: $color-background-white;
      cursor: pointer;
      transition: 0.4s;
      &:hover {
        transform: scale(1.1);
      }
    }
    .Register {
      background: blue;
    }
    .Inscription {
      background: rgb(0, 162, 255);
    }
  }
}

@media screen and (max-width: 1600px) {
  .navContainer {
    .NavInContainer {
      .left {
        .openMenu {
          display: block;
        }
        .linkDiv,
        .Title {
          display: none;
        }
      }
    }
  }
}
